class ServerEnvironment {
  constructor() {
    this.transport = null   // 'stdio' | 'http' | 'sse'
    this.host = null       // 'localhost' | '0.0.0.0' ç­‰
    this.port = null       // ç«¯å£å·ï¼ˆstdioæ¨¡å¼ä¸ºnullï¼‰
    this.processId = null  // è¿›ç¨‹IDï¼Œç”¨äºç”ŸæˆmcpId
    this.initialized = false
  }

  /**
   * åˆå§‹åŒ–æœåŠ¡ç¯å¢ƒï¼ˆå„å¯åŠ¨æ¸ é“è°ƒç”¨ä¸€æ¬¡ï¼‰
   * @param {Object} config - é…ç½®å¯¹è±¡
   * @param {string} config.transport - ä¼ è¾“åè®®
   * @param {string} config.host - ä¸»æœºåœ°å€ï¼ˆå¯é€‰ï¼‰
   * @param {number} config.port - ç«¯å£å·ï¼ˆå¯é€‰ï¼‰
   */
  initialize(config) {
    this.transport = config.transport
    this.host = config.host || null
    this.port = config.port || null
    this.processId = process.pid
    this.initialized = true
  }

  /**
   * è·å–MCP IDï¼ˆåŸºäºprocessIdç”Ÿæˆï¼‰
   * @returns {string} MCPè¿›ç¨‹ID
   */
  getMcpId() {
    if (!this.initialized) {
      throw new Error('ServerEnvironment not initialized')
    }
    return `mcp-${this.processId}`
  }

  /**
   * è·å–ä¼ è¾“åè®®
   * @returns {string} transportç±»å‹
   */
  getTransport() {
    if (!this.initialized) {
      throw new Error('ServerEnvironment not initialized')
    }
    return this.transport
  }

  /**
   * è·å–æœåŠ¡å™¨åœ°å€ä¿¡æ¯ï¼ˆä»…HTTP/SSEæ¨¡å¼ï¼‰
   * @returns {Object|null} {host, port} æˆ– null
   */
  getServerAddress() {
    if (!this.initialized) {
      throw new Error('ServerEnvironment not initialized')
    }
    if (this.transport === 'stdio') {
      return null
    }
    return {
      host: this.host,
      port: this.port
    }
  }

  /**
   * æ£€æŸ¥æ˜¯å¦å·²åˆå§‹åŒ–
   * @returns {boolean}
   */
  isInitialized() {
    return this.initialized
  }
}

// åˆ›å»ºå…¨å±€å•ä¾‹å®ä¾‹
let globalServerEnvironment = null

/**
 * è·å–å…¨å±€ServerEnvironmentå•ä¾‹
 * @returns {ServerEnvironment} å…¨å±€ServerEnvironmentå®ä¾‹
 */
function getGlobalServerEnvironment() {
  if (!globalServerEnvironment) {
    globalServerEnvironment = new ServerEnvironment()
  }
  return globalServerEnvironment
}

module.exports = ServerEnvironment
module.exports.getGlobalServerEnvironment = getGlobalServerEnvironment