const ResourceProtocol = require('./ResourceProtocol')
const fs = require('fs-extra')
const path = require('path')

/**
 * æ€ç»´æ¨¡å¼åè®®å¤„ç†å™¨
 * å¤„ç† thought:// åè®®çš„èµ„æºè§£æ
 */
class ThoughtProtocol extends ResourceProtocol {
  constructor () {
    super('thought')
    this.registry = {}
    this.registryManager = null // ç»Ÿä¸€æ³¨å†Œè¡¨ç®¡ç†å™¨
  }

  /**
   * è®¾ç½®æ³¨å†Œè¡¨ç®¡ç†å™¨
   */
  setRegistryManager(manager) {
    this.registryManager = manager
  }

  /**
   * è®¾ç½®æ³¨å†Œè¡¨
   */
  setRegistry (registry) {
    this.registry = registry || {}
  }

  /**
   * è·å–åè®®ä¿¡æ¯
   */
  getProtocolInfo () {
    return {
      name: 'thought',
      description: 'æ€ç»´æ¨¡å¼èµ„æºåè®®',
      location: 'thought://{thought_id}',
      examples: [
        'thought://prompt-developer',
        'thought://product-owner'
      ]
    }
  }

  /**
   * è§£ææ€ç»´åè®®
   * @param {string} thoughtPath - æ€ç»´è·¯å¾„ï¼Œå¦‚ 'remember'
   * @param {Object} queryParams - æŸ¥è¯¢å‚æ•°ï¼ˆæš‚æœªä½¿ç”¨ï¼‰
   * @returns {Promise<string>} æ€ç»´æ–‡ä»¶å†…å®¹
   */
  async resolve(thoughtPath, queryParams = {}) {
    try {
      // æ„å»ºå¯èƒ½çš„èµ„æºIDæ ¼å¼
      const fullResourceId = `thought:${thoughtPath}`
      
      // ä»RegistryDataæŸ¥æ‰¾èµ„æº
      let resourceData = this.registryManager.registryData.findResourceById(thoughtPath, 'thought')
      
      if (!resourceData) {
        // å¦‚æœæ²¡æ‰¾åˆ°ï¼Œå°è¯•å…¶ä»–æ ¼å¼
        resourceData = this.registryManager.registryData.findResourceById(fullResourceId)
      }
      
      if (!resourceData) {
        const availableThoughts = this.registryManager.registryData.getResourcesByProtocol('thought')
          .map(r => r.id).join(', ')
        throw new Error(`æ€ç»´æ¨¡å¼ '${thoughtPath}' æœªæ‰¾åˆ°ã€‚å¯ç”¨æ€ç»´æ¨¡å¼: ${availableThoughts}`)
      }

      // é€šè¿‡ResourceManageråŠ è½½å®é™…å†…å®¹
      const result = await this.registryManager.loadResourceByProtocol(resourceData.reference)
      
      return result
    } catch (error) {
      throw new Error(`ThoughtProtocol.resolve failed: ${error.message}`)
    }
  }

  /**
   * åŠ è½½èµ„æºå†…å®¹
   */
  async loadContent (resolvedPath, queryParams) {
    try {
      const content = await fs.readFile(resolvedPath, 'utf-8')
      return content
    } catch (error) {
      throw new Error(`æ— æ³•åŠ è½½æ€ç»´æ¨¡å¼æ–‡ä»¶ ${resolvedPath}: ${error.message}`)
    }
  }

  /**
   * éªŒè¯èµ„æºè·¯å¾„
   */
  validatePath (resourcePath) {
    return /^[a-zA-Z0-9_-]+$/.test(resourcePath)
  }
}

module.exports = ThoughtProtocol
