const ResourceProtocol = require('./ResourceProtocol')
const path = require('path')
const fs = require('fs').promises

/**
 * æ–‡ä»¶åè®®å®ç°
 * å®ç°@file://åè®®ï¼Œç”¨äºè®¿é—®æœ¬åœ°æ–‡ä»¶ç³»ç»Ÿä¸­çš„æ–‡ä»¶
 */
class FileProtocol extends ResourceProtocol {
  constructor (options = {}) {
    super('file', options)
  }

  /**
   * è®¾ç½®æ³¨å†Œè¡¨ï¼ˆä¿æŒä¸å…¶ä»–åè®®çš„ä¸€è‡´æ€§ï¼‰
   */
  setRegistry (registry) {
    // Fileåè®®ä¸ä½¿ç”¨æ³¨å†Œè¡¨ï¼Œä½†ä¸ºäº†ä¸€è‡´æ€§æä¾›æ­¤æ–¹æ³•
    this.registry = registry || {}
  }

  /**
   * è·å–åè®®ä¿¡æ¯
   * @returns {object} åè®®ä¿¡æ¯
   */
  getProtocolInfo () {
    return {
      name: 'file',
      description: 'æ–‡ä»¶ç³»ç»Ÿåè®®ï¼Œæä¾›æœ¬åœ°æ–‡ä»¶è®¿é—®',
      location: 'file://{path}',
      examples: [
        'file://package.json',
        'file:///absolute/path/to/file.txt',
        'file://./relative/path/file.md',
        'file://../parent/file.json'
      ],
      params: this.getSupportedParams()
    }
  }

  /**
   * æ”¯æŒçš„æŸ¥è¯¢å‚æ•°
   * @returns {object} å‚æ•°è¯´æ˜
   */
  getSupportedParams () {
    return {
      ...super.getSupportedParams(),
      encoding: 'string - æ–‡ä»¶ç¼–ç  (utf8, ascii, binaryç­‰)',
      exists: 'boolean - ä»…è¿”å›å­˜åœ¨çš„æ–‡ä»¶'
    }
  }

  /**
   * éªŒè¯æ–‡ä»¶åè®®è·¯å¾„
   * @param {string} resourcePath - èµ„æºè·¯å¾„
   * @returns {boolean} æ˜¯å¦æœ‰æ•ˆ
   */
  validatePath (resourcePath) {
    if (!super.validatePath(resourcePath)) {
      return false
    }

    // åŸºæœ¬è·¯å¾„éªŒè¯ - å…è®¸ç›¸å¯¹è·¯å¾„å’Œç»å¯¹è·¯å¾„
    return typeof resourcePath === 'string' && resourcePath.length > 0
  }

  /**
   * è§£ææ–‡ä»¶è·¯å¾„
   * @param {string} resourcePath - åŸå§‹èµ„æºè·¯å¾„
   * @param {QueryParams} queryParams - æŸ¥è¯¢å‚æ•°
   * @returns {Promise<string>} è§£æåçš„ç»å¯¹è·¯å¾„
   */
  async resolvePath (resourcePath, queryParams) {
    let resolvedPath

    if (path.isAbsolute(resourcePath)) {
      // ç»å¯¹è·¯å¾„ç›´æ¥ä½¿ç”¨
      resolvedPath = resourcePath
    } else {
      // ç›¸å¯¹è·¯å¾„ç›¸å¯¹äºå½“å‰å·¥ä½œç›®å½•è§£æ
      resolvedPath = path.resolve(process.cwd(), resourcePath)
    }

    // è§„èŒƒåŒ–è·¯å¾„
    resolvedPath = path.normalize(resolvedPath)

    return resolvedPath
  }

  /**
   * åŠ è½½èµ„æºå†…å®¹
   * @param {string} resolvedPath - è§£æåçš„è·¯å¾„
   * @param {QueryParams} queryParams - æŸ¥è¯¢å‚æ•°
   * @returns {Promise<string>} èµ„æºå†…å®¹
   */
  async loadContent (resolvedPath, queryParams) {
    try {
      // æ£€æŸ¥è·¯å¾„æ˜¯å¦å­˜åœ¨
      const stats = await fs.stat(resolvedPath)

      if (stats.isDirectory()) {
        return await this.loadDirectoryContent(resolvedPath, queryParams)
      } else if (stats.isFile()) {
        return await this.loadFileContent(resolvedPath, queryParams)
      } else {
        throw new Error(`ä¸æ”¯æŒçš„æ–‡ä»¶ç±»å‹: ${resolvedPath}`)
      }
    } catch (error) {
      if (error.code === 'ENOENT') {
        // å¦‚æœè®¾ç½®äº†existså‚æ•°ä¸ºfalseï¼Œè¿”å›ç©ºå†…å®¹è€Œä¸æ˜¯é”™è¯¯
        if (queryParams && queryParams.get('exists') === 'false') {
          return ''
        }
        throw new Error(`æ–‡ä»¶æˆ–ç›®å½•ä¸å­˜åœ¨: ${resolvedPath}`)
      }
      throw error
    }
  }

  /**
   * åŠ è½½æ–‡ä»¶å†…å®¹
   * @param {string} filePath - æ–‡ä»¶è·¯å¾„
   * @param {QueryParams} queryParams - æŸ¥è¯¢å‚æ•°
   * @returns {Promise<string>} æ–‡ä»¶å†…å®¹
   */
  async loadFileContent (filePath, queryParams) {
    const encoding = queryParams?.get('encoding') || 'utf8'
    return await fs.readFile(filePath, encoding)
  }

  /**
   * åŠ è½½ç›®å½•å†…å®¹
   * @param {string} dirPath - ç›®å½•è·¯å¾„
   * @param {QueryParams} queryParams - æŸ¥è¯¢å‚æ•°
   * @returns {Promise<string>} ç›®å½•å†…å®¹åˆ—è¡¨
   */
  async loadDirectoryContent (dirPath, queryParams) {
    const entries = await fs.readdir(dirPath, { withFileTypes: true })

    // åº”ç”¨ç±»å‹è¿‡æ»¤
    const typeFilter = queryParams?.get('type')
    let filteredEntries = entries

    if (typeFilter) {
      filteredEntries = entries.filter(entry => {
        switch (typeFilter) {
          case 'file': return entry.isFile()
          case 'dir': return entry.isDirectory()
          case 'both': return true
          default: return true
        }
      })
    }

    // æ ¼å¼åŒ–è¾“å‡º
    const format = queryParams?.get('format') || 'list'

    switch (format) {
      case 'json':
        return JSON.stringify(
          filteredEntries.map(entry => ({
            name: entry.name,
            type: entry.isDirectory() ? 'directory' : 'file',
            path: path.join(dirPath, entry.name)
          })),
          null,
          2
        )

      case 'paths':
        return filteredEntries
          .map(entry => path.join(dirPath, entry.name))
          .join('\n')

      case 'list':
      default:
        return filteredEntries
          .map(entry => {
            const type = entry.isDirectory() ? '[DIR]' : '[FILE]'
            return `${type} ${entry.name}`
          })
          .join('\n')
    }
  }
}

module.exports = FileProtocol