/**
 * åŒ…çº§èµ„æºå‘ç°å™¨ - ä» @promptx/resource åŒ…åŠ è½½ç³»ç»Ÿå†…ç½®èµ„æº
 * æ–°ç‰ˆæœ¬ï¼šç›´æ¥ä» npm åŒ…åŠ è½½ï¼Œä¸å†ä¾èµ–æ–‡ä»¶ç³»ç»Ÿæ‰«æ
 */

const BaseDiscovery = require('./BaseDiscovery')
const logger = require('@promptx/logger')

/**
 * åŒ…çº§èµ„æºå‘ç°å™¨
 * è´Ÿè´£ä» @promptx/resource åŒ…åŠ è½½ç³»ç»Ÿå†…ç½®çš„è§’è‰²ã€å·¥å…·ç­‰èµ„æº
 */
class PackageDiscovery extends BaseDiscovery {
  constructor(resourceManager) {
    super('PACKAGE')
    this.resourceManager = resourceManager
  }

  /**
   * å‘ç°åŒ…çº§èµ„æº - ä» @promptx/resource åŒ…åŠ è½½
   * @returns {Promise<Array>} å‘ç°çš„èµ„æºåˆ—è¡¨
   */
  async discover() {
    try {
      // ä½¿ç”¨æ–°çš„ @promptx/resource API
      const { registry } = require('@promptx/resource')
      
      if (!registry) {
        logger.warn('[PackageDiscovery] @promptx/resource æ³¨å†Œè¡¨æœªæ­£ç¡®åŠ è½½')
        return []
      }
      const resources = []
      
      // v2.0.0 æ ¼å¼ï¼šresources æ˜¯æ•°ç»„
      if (Array.isArray(registry.resources)) {
        for (const resource of registry.resources) {
          resources.push({
            id: resource.id,
            type: resource.protocol,  // ä½¿ç”¨ protocol å­—æ®µ
            path: resource.metadata?.path || resource.reference,
            name: resource.name || resource.id,
            metadata: {
              description: resource.description,
              modified: resource.metadata?.modified,
              size: resource.metadata?.size,
              source: 'package',  // å°å†™ä»¥ä¿æŒä¸€è‡´
              packageName: '@promptx/resource'
            }
          })
        }
      }
      
      logger.info(`[PackageDiscovery] âœ… ä» @promptx/resource åŠ è½½äº† ${resources.length} ä¸ªç³»ç»Ÿèµ„æº`)
      return resources

    } catch (error) {
      // å¦‚æœåŒ…ä¸å­˜åœ¨æˆ–åŠ è½½å¤±è´¥ï¼Œè¿”å›ç©ºæ•°ç»„ï¼ˆä¸é˜»å¡å…¶ä»–å‘ç°å™¨ï¼‰
      logger.warn(`[PackageDiscovery] âš ï¸ åŠ è½½ @promptx/resource å¤±è´¥: ${error.message}`)
      return []
    }
  }

  /**
   * å‘ç°åŒ…çº§èµ„æºæ³¨å†Œè¡¨
   * @returns {Promise<Map>} èµ„æºæ³¨å†Œè¡¨ Map<resourceId, reference>
   */
  async discoverRegistry() {
    try {
      // ä½¿ç”¨æ–°çš„ @promptx/resource API
      const { registry } = require('@promptx/resource')
      
      if (!registry) {
        logger.warn('[PackageDiscovery] @promptx/resource æ³¨å†Œè¡¨æœªæ­£ç¡®åŠ è½½')
        return new Map()
      }

      const registryMap = new Map()
      
      // v2.0.0 æ ¼å¼ï¼šresources æ˜¯æ•°ç»„
      if (Array.isArray(registry.resources)) {
        for (const resource of registry.resources) {
          // æ·»åŠ å¤šç§å¼•ç”¨æ ¼å¼
          const reference = resource.reference || `@package://resources/${resource.metadata?.path}`
          registryMap.set(resource.id, reference)
          registryMap.set(`package:${resource.id}`, reference)
        }
      }
      
      if (registryMap.size > 0) {
        logger.info(`[PackageDiscovery] âœ… ä» @promptx/resource åŠ è½½äº† ${registryMap.size / 2} ä¸ªç³»ç»Ÿèµ„æºåˆ°æ³¨å†Œè¡¨`)
      }
      
      return registryMap

    } catch (error) {
      logger.warn(`[PackageDiscovery] âš ï¸ ç³»ç»Ÿèµ„æºæ³¨å†Œè¡¨åŠ è½½å¤±è´¥: ${error.message}`)
      return new Map()
    }
  }

  /**
   * è·å–åŒ…èµ„æºçš„åŸºç¡€ç›®å½•ï¼ˆç”¨äºæ–‡ä»¶è®¿é—®ï¼‰
   * @returns {Promise<string>} åŒ…èµ„æºç›®å½•è·¯å¾„
   */
  async getPackageRoot() {
    try {
      // è·å– @promptx/resource åŒ…çš„å®é™…è·¯å¾„
      const resourcePackagePath = require.resolve('@promptx/resource')
      const path = require('path')
      
      // æ‰¾åˆ°åŒ…çš„æ ¹ç›®å½•ï¼ˆåŒ…å« package.json çš„ç›®å½•ï¼‰
      let currentDir = path.dirname(resourcePackagePath)
      while (currentDir !== path.dirname(currentDir)) {
        const packageJsonPath = path.join(currentDir, 'package.json')
        try {
          const packageJson = require(packageJsonPath)
          if (packageJson.name === '@promptx/resource') {
            return currentDir
          }
        } catch {
          // ç»§ç»­å‘ä¸ŠæŸ¥æ‰¾
        }
        currentDir = path.dirname(currentDir)
      }
      
      throw new Error('æ— æ³•æ‰¾åˆ° @promptx/resource åŒ…çš„æ ¹ç›®å½•')
    } catch (error) {
      logger.error(`[PackageDiscovery] âŒ è·å–åŒ…æ ¹ç›®å½•å¤±è´¥: ${error.message}`)
      throw error
    }
  }

  /**
   * è·å–æ³¨å†Œè¡¨æ•°æ®ï¼ˆResourceManager éœ€è¦çš„æ–¹æ³•ï¼‰
   * @returns {Promise<RegistryData>} æ³¨å†Œè¡¨æ•°æ®å®ä¾‹
   */
  async getRegistryData() {
    try {
      logger.info('[PackageDiscovery] Starting getRegistryData...')
      const { registry } = require('@promptx/resource')
      logger.info('[PackageDiscovery] @promptx/resource loaded successfully')
      const RegistryData = require('../RegistryData')
      const ResourceData = require('../ResourceData')
      
      if (!registry) {
        logger.warn('[PackageDiscovery] Registry is empty')
        return new RegistryData('package', '', [])
      }
      
      logger.info(`[PackageDiscovery] Registry loaded with ${registry.resources?.length || 0} resources`)
      const resources = []
      
      // v2.0.0 æ ¼å¼ï¼šresources æ˜¯æ•°ç»„ï¼Œç›´æ¥å¤„ç†
      if (Array.isArray(registry.resources)) {
        for (const resource of registry.resources) {
          resources.push(new ResourceData({
            id: resource.id,
            source: 'package',  // ä½¿ç”¨å°å†™ä¿æŒä¸€è‡´
            protocol: resource.protocol,  // ç›´æ¥ä½¿ç”¨èµ„æºçš„ protocol å­—æ®µ
            name: resource.name || resource.id,
            description: resource.description || '',
            reference: resource.reference,
            metadata: resource.metadata || {}
          }))
        }
      }
      
      logger.info(`[PackageDiscovery] Successfully created ${resources.length} ResourceData objects`)
      return new RegistryData('package', '@promptx/resource', resources)
    } catch (error) {
      logger.error(`[PackageDiscovery] Error in getRegistryData: ${error.message}`)
      logger.error(`[PackageDiscovery] Stack trace: ${error.stack}`)
      logger.warn(`[PackageDiscovery] è·å–æ³¨å†Œè¡¨æ•°æ®å¤±è´¥: ${error.message}`)
      const RegistryData = require('../RegistryData')
      return new RegistryData('package', '', [])
    }
  }

  /**
   * è·å–ç¯å¢ƒä¿¡æ¯ï¼ˆç”¨äºè°ƒè¯•ï¼‰
   */
  getEnvironmentInfo() {
    return {
      type: 'PackageDiscovery',
      source: '@promptx/resource',
      loaded: this._tryRequirePackage() !== null
    }
  }

  /**
   * å°è¯•åŠ è½½åŒ…ï¼ˆå†…éƒ¨è¾…åŠ©æ–¹æ³•ï¼‰
   */
  _tryRequirePackage() {
    try {
      const { registry } = require('@promptx/resource')
      return registry ? { registry } : null
    } catch {
      return null
    }
  }
}

module.exports = PackageDiscovery