const ResourceProtocol = require('./ResourceProtocol')
const fs = require('fs-extra')
const path = require('path')

/**
 * AIè§’è‰²åè®®å¤„ç†å™¨
 * å¤„ç† role:// åè®®çš„èµ„æºè§£æï¼Œç›´æ¥åŠ è½½å®Œæ•´roleæ–‡ä»¶
 */
class RoleProtocol extends ResourceProtocol {
  constructor () {
    super('role')
    this.registry = {}
    this.registryManager = null // ç»Ÿä¸€æ³¨å†Œè¡¨ç®¡ç†å™¨
  }

  /**
   * è®¾ç½®æ³¨å†Œè¡¨ç®¡ç†å™¨
   */
  setRegistryManager(manager) {
    this.registryManager = manager
  }

  /**
   * è®¾ç½®æ³¨å†Œè¡¨
   */
  setRegistry (registry) {
    this.registry = registry || {}
  }

  /**
   * è·å–åè®®ä¿¡æ¯
   */
  getProtocolInfo () {
    return {
      name: 'role',
      description: 'AIè§’è‰²èµ„æºåè®®',
      location: 'role://{role_id}',
      examples: [
        'role://video-copywriter',
        'role://product-owner',
        'role://assistant',
        'role://prompt-developer'
      ]
    }
  }

  /**
   * è§£æè§’è‰²åè®®
   * @param {string} rolePath - è§’è‰²è·¯å¾„ï¼Œå¦‚ 'java-developer'
   * @param {Object} queryParams - æŸ¥è¯¢å‚æ•°ï¼ˆæš‚æœªä½¿ç”¨ï¼‰
   * @returns {Promise<string>} è§’è‰²æ–‡ä»¶å†…å®¹
   */
  async resolve(rolePath, queryParams = {}) {
    try {
      // æ„å»ºå¯èƒ½çš„èµ„æºIDæ ¼å¼
      const fullResourceId = `role:${rolePath}`
      const shortResourceId = rolePath
      
      // ä»RegistryDataæŸ¥æ‰¾èµ„æº
      let resourceData = this.registryManager.registryData.findResourceById(rolePath, 'role')
      
      if (!resourceData) {
        // å¦‚æœæ²¡æ‰¾åˆ°ï¼Œå°è¯•å…¶ä»–æ ¼å¼
        resourceData = this.registryManager.registryData.findResourceById(fullResourceId)
      }
      
      if (!resourceData) {
        const availableRoles = this.registryManager.registryData.getResourcesByProtocol('role')
          .map(r => r.id).join(', ')
        throw new Error(`è§’è‰² '${rolePath}' æœªæ‰¾åˆ°ã€‚å¯ç”¨è§’è‰²: ${availableRoles}`)
      }

      // é€šè¿‡ResourceManageråŠ è½½å®é™…å†…å®¹
      const result = await this.registryManager.loadResourceByProtocol(resourceData.reference)
      
      return result
    } catch (error) {
      throw new Error(`RoleProtocol.resolve failed: ${error.message}`)
    }
  }

  /**
   * åŠ è½½èµ„æºå†…å®¹
   */
  async loadContent (resolvedPath, queryParams) {
    try {
      const content = await fs.readFile(resolvedPath, 'utf-8')
      return content
    } catch (error) {
      throw new Error(`æ— æ³•åŠ è½½è§’è‰²æ–‡ä»¶ ${resolvedPath}: ${error.message}`)
    }
  }

  /**
   * éªŒè¯èµ„æºè·¯å¾„
   */
  validatePath (resourcePath) {
    return /^[a-zA-Z0-9_-]+$/.test(resourcePath)
  }
}

module.exports = RoleProtocol
